.experiences {
  background: linear-gradient(180deg, 
    rgba(255, 255, 255, 1) 0%, 
    rgba(244, 228, 188, 0.2) 50%,
    rgba(255, 255, 255, 1) 100%
  );
}

/* Section spéciale pour les plages */
.featured-experience {
  margin-bottom: 80px;
  padding: 40px;
  background: linear-gradient(135deg, rgba(19, 117, 183, 0.05), rgba(244, 228, 188, 0.1));
  border-radius: 25px;
  border: 1px solid rgba(19, 117, 183, 0.1);
}

.featured-header {
  text-align: center;
  margin-bottom: 50px;
}

.featured-header h3 {
  font-size: 2.5rem;
  color: var(--tunisia-blue);
  margin-bottom: 15px;
  font-weight: 700;
}

.featured-header p {
  font-size: 1.2rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

.beach-experiences-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 30px;
}

.beach-card {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  transition: all 0.3s ease;
  border: 1px solid rgba(19, 117, 183, 0.1);
}

.beach-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 50px rgba(0,0,0,0.15);
}

.beach-image {
  position: relative;
  height: 250px;
  overflow: hidden;
}

.beach-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.beach-card:hover .beach-image img {
  transform: scale(1.05);
}

.beach-overlay {
  position: absolute;
  top: 15px;
  right: 15px;
}

.location-tag {
  background: rgba(19, 117, 183, 0.9);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  backdrop-filter: blur(10px);
}

.beach-content {
  padding: 30px;
}

.beach-content h4 {
  font-size: 1.4rem;
  color: var(--tunisia-blue);
  margin-bottom: 10px;
  font-weight: 700;
}

.beach-content p {
  color: var(--text-secondary);
  margin-bottom: 20px;
  line-height: 1.6;
}

.beach-activities {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 25px;
}

.activity-tag {
  background: rgba(244, 228, 188, 0.3);
  color: var(--tunisia-blue);
  padding: 6px 12px;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: 500;
  border: 1px solid rgba(19, 117, 183, 0.2);
}

.btn-beach-discover {
  background: linear-gradient(135deg, var(--tunisia-blue), var(--tunisia-sunset));
  color: white;
  border: none;
  padding: 12px 25px;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  width: 100%;
  justify-content: center;
}

.btn-beach-discover:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(19, 117, 183, 0.3);
}

.btn-beach-discover .arrow {
  transition: transform 0.3s ease;
}

.btn-beach-discover:hover .arrow {
  transform: translateX(5px);
}

.experiences-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 40px;
  margin-bottom: 80px;
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

.experience-category {
  background: white;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  border: 1px solid #f0f0f0;
  position: relative;
}

.experience-category:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.15);
}

.experience-category::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(194, 54, 54, 0.1), transparent);
  transition: left 0.5s;
  z-index: 1;
}

.experience-category:hover::before {
  left: 100%;
}

.category-header {
  position: relative;
  z-index: 2;
  padding: 40px 30px 20px;
  background: linear-gradient(135deg, var(--tunisia-red) 0%, var(--tunisia-gold) 100%);
  color: white;
  display: block;
}

.category-icon {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.5rem;
  margin-bottom: 20px;
  background: rgba(255, 255, 255, 0.2) !important;
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  flex-shrink: 0;
}

.category-info {
  flex: 1;
}

.category-info h3 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 8px;
  color: white;
}

.category-info h4 {
  font-size: 1.1rem;
  margin-bottom: 12px;
  opacity: 0.95;
  font-weight: 500;
  color: white;
}

.category-info p {
  font-size: 1rem;
  opacity: 0.9;
  line-height: 1.5;
  margin: 0;
  color: white;
}

.category-experiences {
  margin-bottom: 25px;
  padding: 30px;
  position: relative;
  z-index: 2;
}

.category-experiences ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.category-experiences li {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  color: #666;
  font-size: 0.95rem;
  line-height: 1.4;
  padding: 8px 0;
  border-bottom: 1px solid #f0f0f0;
}

.category-experiences li:last-child {
  border-bottom: none;
}

.experience-bullet {
  color: var(--tunisia-red);
  font-weight: bold;
  font-size: 1rem;
}

.category-cta {
  display: flex;
  justify-content: center;
  padding: 0 30px 30px;
  position: relative;
  z-index: 2;
}

.btn-discover {
  background: linear-gradient(45deg, var(--tunisia-red), var(--tunisia-gold));
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.btn-discover:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 25px rgba(194, 54, 54, 0.3);
}

.btn-discover .arrow {
  transition: transform 0.3s ease;
  font-size: 1.2rem;
}

.btn-discover:hover .arrow {
  transform: translateX(5px);
}

.experiences-highlight {
  background: linear-gradient(135deg, 
    rgba(30, 58, 138, 0.05), 
    rgba(196, 30, 58, 0.05)
  );
  border-radius: 25px;
  padding: 50px;
  border: 1px solid rgba(30, 58, 138, 0.1);
}

.highlight-content {
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 50px;
  align-items: center;
}

.highlight-left h3 {
  color: var(--tunisia-blue);
  font-size: 2.2rem;
  margin-bottom: 20px;
  font-weight: 700;
}

.highlight-left p {
  color: var(--text-light);
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 30px;
}

.highlight-stats {
  display: flex;
  gap: 40px;
  flex-wrap: wrap;
}

.stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 2.2rem;
  font-weight: 700;
  background: linear-gradient(135deg, var(--tunisia-red), var(--tunisia-sunset));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 5px;
}

.stat-label {
  color: var(--text-light);
  font-size: 0.9rem;
  font-weight: 500;
}

.highlight-right {
  display: flex;
  justify-content: center;
}

.experience-finder {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  text-align: center;
  width: 100%;
  max-width: 300px;
}

.experience-finder h4 {
  color: var(--text-dark);
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-weight: 600;
}

.finder-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
  margin-bottom: 25px;
}

.finder-btn {
  padding: 10px 15px;
  border: 2px solid rgba(196, 30, 58, 0.2);
  background: none;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.85rem;
  font-weight: 500;
  color: var(--text-light);
}

.finder-btn.active,
.finder-btn:hover {
  background: linear-gradient(135deg, var(--tunisia-red), var(--tunisia-sunset));
  color: white;
  border-color: transparent;
  transform: translateY(-2px);
}

/* Mobile Responsive */
@media (max-width: 1024px) {
  .experiences-grid {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 25px;
  }
  
  .highlight-content {
    grid-template-columns: 1fr;
    gap: 40px;
    text-align: center;
  }
  
  .highlight-stats {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .experiences-grid {
    grid-template-columns: 1fr;
  }
  
  .experience-category {
    padding: 25px;
  }
  
  .category-header {
    gap: 15px;
  }
  
  .category-icon {
    width: 50px;
    height: 50px;
    font-size: 1.5rem;
  }
  
  .category-info h3 {
    font-size: 1.2rem;
  }
  
  .category-info h4 {
    font-size: 0.9rem;
  }
  
  .experiences-highlight {
    padding: 30px 25px;
  }
  
  .highlight-left h3 {
    font-size: 1.8rem;
  }
  
  .highlight-stats {
    gap: 25px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  .experience-finder {
    padding: 25px;
  }
  
  .finder-options {
    grid-template-columns: 1fr;
  }
}
